* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: 'Montserrat','IBM Plex Sans', 'Open Sans',sans-serif;
}

.App {
  width: 100%;
  height: 100%;
  /* height: "100vh"; */
  /* background-color: red; */
}

#sidebar nav {
  list-style-type: none;

}

#sidebar nav a {
  list-style-type: none;

  display: flex;
  align-items: center;
  justify-content: space-between;
  overflow: hidden;

  white-space: pre;
  padding: 0.5rem;
  border-radius: 8px;
  color: inherit;
  text-decoration: none;
  gap: 1rem;
}

#sidebar nav a:hover {
  /* background: #e3e3e3; */
}

#sidebar nav a.active {
  /* background: hsl(224, 98%, 58%); */
  background: rgba(255, 255, 255, 0.15);
  color: white;

}

#sidebar nav a.pending {
  /* color: hsl(224, 98%, 58%); */
  color: rgba(255, 255, 255, 0.15);
}

#sidebar nav ul {
list-style-type: none;
}